  box-shadow: 0 0 0 2px #FFFFFF, 0 0 0 4px #FFA601; }

.radio__input:disabled + .radio__box,
.radio__input:disabled ~ .radio__text {
  opacity: 0.5; }

.chronic-disease {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 2rem;
  padding-left: 9px;
  padding-right: 2rem;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.429;
  color: #40B3B7;
  background-color: #DAFBF1;
  border-radius: 4px; }

.chronic-disease__button {
  position: absolute;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  padding: 0;
  background: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s; }
  .chronic-disease__button:focus {
    outline: none;
    box-shadow: 0 0 0 2px #FFFFFF, 0 0 0 4px #FFA601; }

.chronic-disease__button > svg {
  width: 0.5rem;
  height: 0.5rem; }
